// src/main.js
import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
//全局样式
import './assets/index.css';
//引入element-plus
import ElementPlus from 'element-plus';
import 'element-plus/dist/index.css';
// 引入数据文件
import { menus0, menus3, menus4, menus5, menus6, menus7, menus8 } from './data/data';

const app = createApp(App);

// 将数据设置为全局属性
app.config.globalProperties.$menus0 = menus0;
app.config.globalProperties.$menus3 = menus3;
app.config.globalProperties.$menus4 = menus4;
app.config.globalProperties.$menus5 = menus5;
app.config.globalProperties.$menus6 = menus6;
app.config.globalProperties.$menus7 = menus7;
app.config.globalProperties.$menus8 = menus8;

app.use(ElementPlus);
app.use(router);
app.mount('#app');
