<template>
    <div>
        <div v-if="windowShow1" class="center-container">
            <h1 class="center-text">请使用移动端浏览</h1>
        </div>
        <div v-if="windowShow2">
            <div class="fixed-header">
                <div class="flex-container">
                    <img class="logo" src="../../public/5.png" alt="" @click="goToHome">
                    <span class="shrinkage">
                        <el-input v-model="input" placeholder="请输入查询内容" />
                        <img class="searchImg" src="../../public/00.png" alt="" @click="searchQuery(input)">
                        <span class="shrinkage1" @click="shrinkageMenu">{{ contentMain }}</span>
                    </span>
                </div>
            </div>
            <div v-if="showOverlay" class="overlay" @click="hideOverlay">
                <Menus></Menus>
            </div>
            <div v-else style="padding-top: 60px;">
                <div class="result-container1" v-if="matchingMenus.length > 0">
                    <div v-for="menu in matchingMenus">
                        <a :href="menu.url">
                            <div :key="menu.id" class="result-item1">
                                {{ menu.title }}
                            </div>
                        </a>
                    </div>
                </div>
                <router-view />
            </div>
        </div>
    </div>
</template>
  
<script>
// 菜单
import Menus from '@/views/0.vue'

export default {
    components: { Menus },
    data() {
        return {
            windowShow1: false,
            windowShow2: false,
            input: '',
            contentMain: ' 三 ',
            showOverlay: false,
            matchingMenus: [], // 用于存储匹配结果的数组
        }
    },
    mounted() {
        // 监听页面宽度，判断移动端浏览或PC端浏览
        window.addEventListener('resize', this.handleWindowResize);
        this.handleWindowResize();
    },
    beforeDestroy() {
        // 移除页面宽度监听
        window.removeEventListener('resize', this.handleWindowResize);
    },
    methods: {
        // 监听页面宽度
        handleWindowResize() {
            var width = window.innerWidth;
            this.windowShow1 = width > 550; // 大于550，输出结果true
            this.windowShow2 = !this.windowShow1; // 小于550，输出结果false
        },
        // 菜单伸缩
        shrinkageMenu() {
            this.contentMain = this.contentMain === ' 三 ' ? ' X ' : ' 三 ';
            this.showOverlay = !this.showOverlay;
            this.matchingMenus.length = 0;
        },
        // 隐藏菜单
        hideOverlay() {
            this.showOverlay = false;
            this.contentMain = this.contentMain === ' 三 ' ? ' X ' : ' 三 ';
        },
        // 点击LOGO返回首页
        goToHome() {
            this.$router.push('/');
        },
        // 搜索输入框
        searchQuery(query) {
            // 将输入内容转换为小写以进行不区分大小写的匹配
            const keyword = query.trim().toLowerCase();

            if (!keyword) {
                alert("请输入查询内容");
                return;
            }

            // 数据中进行模糊搜索
            const allMenus = [
                ...this.$menus0,
                ...this.$menus3,
                ...this.$menus4,
                ...this.$menus5,
                ...this.$menus6,
                ...this.$menus7,
                ...this.$menus8,
            ];

            const matchingMenus = allMenus.filter(menu =>
                menu.title.toLowerCase().includes(keyword)
            );

            this.matchingMenus = matchingMenus; // 更新匹配结果数组

            if (this.matchingMenus.length === 0) {
                alert("没有符合条件的数据")
            }
        }
    }
}
</script>

<style scoped>
.overlay {
    position: fixed;
    left: 0;
    width: 100%;
    top: 60px;
    max-height: calc(100vh - 60px);
    background-color: rgb(255, 255, 255);
    color: #a16e6e;
    z-index: 1000;
    overflow-y: auto;
}

.fixed-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    text-align: center;
    z-index: 1000;
}

.center-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.center-text {
    text-align: center;
}

.logo {
    padding: 10px;
    width: 30%;
    height: auto;
}

.flex-container {
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
}

.shrinkage {
    display: flex;
    margin-left: auto;
}

.shrinkage1 {
    margin-right: 20px;
    margin-top: 5px;
}

.searchImg {
    width: 20px;
    height: 20px;
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 6px;
}

.result-container {
    background-color: #f5f5f5;
    border-top: 1px solid #ccc;
    padding: 10px;
}

.result-item {
    padding: 5px;
    border-bottom: 1px solid #ddd;
}

.result-container1 {
    position: fixed;
    top: 60px;
    /* 与搜索栏底部对齐 */
    left: 0;
    width: 100%;
    bottom: 0;
    /* 占满页面底部 */
    background-color: rgb(255, 255, 255);
    color: #a16e6e;
    z-index: 1000;
    overflow-y: auto;
}

.result-item1 {
    display: flex;
    align-items: center;
    padding: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
    margin-left: 10px;
    margin-right: 10px;
    line-height: 30px;
}

a {
    color: inherit;
    text-decoration: none;
}
</style>
